	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Distribution of this new publication \"pseŭda: ka ĝo 0.5\" will be postponed</strong> . Distribution start time is unknown at this stage.</p>\n<p>The new publication of <a href=\"/!/dst/\">C</a> 73 is only <a href=\"/!/dst/\">danmaq soundtrack</a> (100 yen).</p>\n<p>Aside from that, I will <a href=\"/!/thA/\">bring</a> a <a href=\"/!/thA/\">dream</a> <a href=\"/!/thC/\">drama</a> (1,000 yuan), <a href=\"/!/thA/\">Tokijo Castle</a> (CD-R simple binding version / \\ 400 yuan), <a href=\"/!/leila/\">Leila</a> (300 yen) as a published book.</p>\n<p>We would like to apologize deeply to everyone who expected it. Thank you for your understanding.</p>\n<h2>To a certain idea</h2>\n<p>If you bother to go to the neighbors' neighborhood cafe, do not draw a strange thing on the picture board so why did you come to visit us? This morning, it happens to be written on my website, it will be visible and cola can squirrel back from the straw for a moment. ww (Although I did not know it pretended to disappear, I used to have a net cafe for preparing winter comics) No, it is scary accident ...... Well, whatever you say, let me have a funny moment I received.</p>","frontmatter":{"cover":null,"date":"2007-12-30T00:00:00+09:00","strDate":"2007/12/29","title":"[Urgent] apology"}}},"pathContext":{"path":"/en/blog/2007/12/30-緊急おわび/","langKey":"en"}}

